import util from '@/util/we7/resource/js/util.js';
var utiltool = {};
// 
utiltool.debug = true;

/**
 * log
 */
utiltool.mayiwoLog = function(info, title = '') {
	if (!this.debug) {
		return false;
	}
	var pages = getCurrentPages() //
	var currentPage = pages.route; //
	var url = pages.route //url
	var t = "" + url + "";
	if (title != "") {
		t += "[" + title + "]";
	}
	console.log(t);
	console.log(info);
},

/**
 * 
 */
utiltool.form_timefunction = function(a) {
	var myDate = new Date(parseInt(a) * 1000);
	var datestr = "";
	datestr = myDate.getFullYear(); //(4,1970-????)
	datestr = datestr + "-" + ((myDate.getMonth() + 1) < 10 ? "0" + (myDate.getMonth() + 1) : (myDate.getMonth() + 1)); //(0-11,01)
	datestr = datestr + "-" + (myDate.getDate() < 10 ? "0" + myDate.getDate() : myDate.getDate()); //(1-31)
	return datestr;
},
/**
 * 
 */
utiltool.form_time_shortdata_function = function(a) {
	var myDate = new Date(parseInt(a) * 1000);
	var datestr = "";
	datestr = myDate.getFullYear(); //(4,1970-????)
	datestr = ((myDate.getMonth() + 1) < 10 ? "0" + (myDate.getMonth() + 1) : (myDate.getMonth() + 1)); //(0-11,01
	datestr = datestr + "-" + (myDate.getDate() < 10 ? "0" + myDate.getDate() : myDate.getDate()); //(1-31)
	datestr = datestr + " " + (myDate.getHours() < 10 ? "0" + myDate.getHours() : myDate.getHours()); //
	datestr = datestr + ":" + (myDate.getMinutes() < 10 ? "0" + myDate.getMinutes() : myDate.getMinutes()); //
	return datestr;
},

/**
 * url
 */
utiltool.getQuery = function(url) {
	var theRequest = [];
	if (url.indexOf("?") != -1) {
		var str = url.split('?')[1];
		var strs = str.split("&");
		for (var i = 0; i < strs.length; i++) {
			if (strs[i].split("=")[0] && unescape(strs[i].split("=")[1])) {
				theRequest[i] = {
					'name': strs[i].split("=")[0],
					'value': unescape(strs[i].split("=")[1])
				}
			}
		}
	}
	return theRequest;
}

/**
 * 
 * cb 
 */
utiltool.getUserInfo = function(data, cb) {
	var that = this;
	util.getUserInfo(function(res) {
		//
		that.mayiwoLog(wx.getStorageSync("userInfo"), "");
		var newdata = new Object(); //res.memberInfo;
		newdata.from_id = 0;
		//
		if (data.from_id) {
			that.mayiwoLog(data.from_id, "UID");
			newdata.from_id = data.from_id;
		} else {
			that.mayiwoLog(data.from_id, "UID");
		}
		var api = "wxapi";
		newdata.model = "member";
		newdata.apiname = "member_info";
		that.mayiwoPost(api, newdata, function(res) {
			//
			wx.setStorageSync("memberInfo", res.result.data);
			that.mayiwoLog(wx.getStorageSync("memberInfo"), "");
			typeof cb == "function" && cb(res.result.data);
		});
	});
};
