/**
 * post ajax
 * api 
 * data post
 * cb 
 */
utiltool.mayiwoPost = function(api, data, cb) {
	var that = this;
	data.m = "haojk_myw";
	that.mayiwoLog(data.apiname + ":" + utiltool.dateFormat("yyyy-MM-dd hh:mm:ss.S") + "");
	util.request({
		url: 'entry/wxapp/' + api,
		showLoading: false,
		data: data,
		method: 'post',
		success: function(res) {
			//
			var loginfo = {
				"POST API": api,
				"POST ": data,
				"POST ": res.data
			}
			that.mayiwoLog(loginfo);
			that.mayiwoLog(data.apiname + ":" + utiltool.dateFormat("yyyy-MM-dd hh:mm:ss.S") + "");
			typeof cb == "function" && cb(res.data);
		}
	});
},
utiltool.dateFormat = function(fmt) { // author: meizz
	var date = new Date();
	var o = {
		"M+": date.getMonth() + 1, // 
		"d+": date.getDate(), // 
		"h+": date.getHours(), // 
		"m+": date.getMinutes(), // 
		"s+": date.getSeconds(), // 
		"q+": Math.floor((date.getMonth() + 3) / 3), // 
		"S": date.getMilliseconds() // 
	};
	if (/(y+)/.test(fmt))
		fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
	for (var k in o)
		if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[
			k]).substr(("" + o[k]).length)));
	return fmt;
}

