import { base64_encode, base64_decode } from '@/util/we7/resource/js/base64.js';
import md5 from '@/util/we7/resource/js/md5.js';
import siteInfo from '@/util/we7/resource/js/siteinfo.js';
import Request from '@/util/luch-request/request.js';
var util = {};

util.base64Encode = function (str) {
	return base64_encode(str)
}; 

util.base64Decode = function (str) {
	return base64_decode(str)
};

util.md5 = function (str) {
	return md5(str)
};

/**
	, 
	@params action controller, action, do 'wxapp/home/navs'
	@params querystring  {1 : 1, 2 : 2}
*/
util.url = function (action, querystring) {
	var app = getApp();
	var url = siteInfo.siteroot + '?i=' + siteInfo.uniacid + '&t=' + siteInfo.multiid + '&v=' + siteInfo.version + '&from=wxapp&';

	if (action) {
		action = action.split('/');
		if (action[0]) {
			url += 'c=' + action[0] + '&';
		}
		if (action[1]) {
			url += 'a=' + action[1] + '&';
		}
		if (action[2]) {
			url += 'do=' + action[2] + '&';
		}
	}
	if (querystring && typeof querystring === 'object') {
		for (let param in querystring) {
            if (param && querystring.hasOwnProperty(param) && querystring[param]) {
				url += param + '=' + querystring[param] + '&';
			}
		}
	}
	return url;
}

function getQuery(url) {
	var theRequest = [];
	if (url.indexOf("?") != -1) {
		var str = url.split('?')[1];
		var strs = str.split("&");
		for (var i = 0; i < strs.length; i++) {
			if (strs[i].split("=")[0] && unescape(strs[i].split("=")[1])) {
				theRequest[i] = {
					'name': strs[i].split("=")[0],
					'value': unescape(strs[i].split("=")[1])
				}
			}
		}
	}
	return theRequest;
}
/*
* 
* url 
* name 
*/
function getUrlParam(url, name) {
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //  
	var r = url.split('?')[1].match(reg);  //  
	if (r != null) return unescape(r[2]); return null; //  
}
/**
 *  tokenmd5
 * url 
 * date {1 : 1, 2 : 2} *
 * token token 
 */
function getSign(url, data, token) {
	var _ = require('@/util/we7/resource/js/underscore.js');
	var md5 = require('@/util/we7/resource/js/md5.js');
	var querystring = '';
	var sign = getUrlParam(url, 'sign');
	if (sign || (data && data.sign)) {
		return false;
	} else {
		if (url) {
			querystring = getQuery(url);
		}
		if (data) {
			var theRequest = [];
			for (let param in data) {
				if (param && data[param]) {
					theRequest = theRequest.concat({
						'name': param,
						'value': data[param]
					})
				}
			}
			querystring = querystring.concat(theRequest);
		}
		//
		querystring = _.sortBy(querystring, 'name');
		//
		querystring = _.uniq(querystring, true, 'name');
		var urlData = '';
		for (let i = 0; i < querystring.length; i++) {
			if (querystring[i] && querystring[i].name && querystring[i].value) {
				urlData += querystring[i].name + '=' + querystring[i].value;
				if (i < (querystring.length - 1)) {
					urlData += '&';
				}
			}
		}
		token = token ? token : siteInfo.token;
		sign = md5(urlData + token);
		return sign;
	}
}
util.getSign = function (url, data, token) {
	return getSign(url, data, token);
};

