/**
	wx.request

	@params option 
	{
		url : ,
		data : ,
		header : ,
		method : ,
		success : ,
		fail : ,
		complete : ,

		cachetime : http
	}
*/
util.request = function (option) {
	var _ = require('@/util/we7/resource/js/underscore.js');
	var md5 = require('@/util/we7/resource/js/md5.js');
	var app = getApp();
	var option = option ? option : {};
	option.cachetime = option.cachetime ? option.cachetime : 0;
	option.showLoading = typeof option.showLoading != 'undefined' ? option.showLoading : true;
	var sessionid = wx.getStorageSync('userInfo').sessionid;
	var url = option.url;
	if (url.indexOf('http://') == -1 && url.indexOf('https://') == -1) {
		url = util.url(url);
	}
	var state = getUrlParam(url, 'state');
	if (!state && !(option.data && option.data.state) && sessionid) {
		url = url + '&state=we7sid-' + sessionid
	}
	if (!option.data || !option.data.m) {
		var nowPage = getCurrentPages();
   
		if (nowPage.length) {
			nowPage = nowPage[getCurrentPages().length - 1];
      if (nowPage && nowPage.__route__) {
				url = url + '&m=' + nowPage.__route__.split('/')[0];
			}
		}
	}

	var sign = getSign(url, option.data);
	if (sign) {
		url = url + "&sign=" + sign;
	}
	if (!url) {
		return false;
	}
	wx.showNavigationBarLoading();
	if (option.showLoading) {
		util.showLoading();
	}
	if (option.cachetime) {
		var cachekey = md5(url);
		var cachedata = wx.getStorageSync(cachekey);
		var timestamp = Date.parse(new Date());

		if (cachedata && cachedata.data) {
			if (cachedata.expire > timestamp) {
				if (option.complete && typeof option.complete == 'function') {
					option.complete(cachedata);
				}
				if (option.success && typeof option.success == 'function') {
					option.success(cachedata);
				}
				console.log('cache:' + url);
				wx.hideLoading();
				wx.hideNavigationBarLoading();
				return true;
			} else {
				wx.removeStorageSync(cachekey)
			}
		}
	}
	wx.request({
		'url': url,
		'data': option.data ? option.data : {},
		'async': false,
		'header': option.header ? option.header : {},
		'method': option.method ? option.method : 'GET',
		'header': {
			'content-type': 'application/x-www-form-urlencoded'
		},
		'success': function (response) {
			wx.hideNavigationBarLoading();
			wx.hideLoading();
			if (response.data.errno) {
				if (response.data.errno == '41009') {
					wx.setStorageSync('userInfo', '');
					util.getUserInfo(function () {
						util.request(option)
					});
					return;
				} else {
					if (option.fail && typeof option.fail == 'function') {
						option.fail(response);
					} else {
						if (response.data.message) {
							if (response.data.data != null && response.data.data.redirect) {
								var redirect = response.data.data.redirect;
							} else {
								var redirect = '';
							}
							app.util.message(response.data.message, redirect, 'error');
						}
					}
					return;
				}
            } else {
				if (option.success && typeof option.success == 'function') {
					option.success(response);
				}
				//HTTP
				if (option.cachetime) {
					var cachedata = { 'data': response.data, 'expire': timestamp + option.cachetime * 1000 };
					wx.setStorageSync(cachekey, cachedata);
				}
			}
		},
		'fail': function (response) {
			wx.hideNavigationBarLoading();
			wx.hideLoading();
			
			//
			var md5 = require('md5.js');
			var cachekey = md5(url);
			var cachedata = wx.getStorageSync(cachekey);
			if (cachedata && cachedata.data) {
				if (option.success && typeof option.success == 'function') {
					option.success(cachedata);
				}
				console.log('failreadcache:' + url);
				return true;
			} else {
				if (option.fail && typeof option.fail == 'function') {
					option.fail(response);
				}
			}
		},
		'complete': function (response) {
			// wx.hideNavigationBarLoading();
			// wx.hideLoading();
			if (option.complete && typeof option.complete == 'function') {
				option.complete(response);
			}
		}
	});
}
util.getWe7User = function (cb, code) {
	var userInfo = wx.getStorageSync('userInfo') || {};
	util.request({
		url: 'auth/session/openid',
		data: { code: code ? code : '' },
		cachetime: 0,
		showLoading: false,
		success: function (session) {
			if (!session.data.errno) {
				userInfo.sessionid = session.data.data.sessionid
				userInfo.memberInfo = session.data.data.userinfo
				wx.setStorageSync('userInfo', userInfo)
			}
			typeof cb == "function" && cb(userInfo);
		}
	});
}
util.upadteUser = function (wxInfo, cb) {
	
	var userInfo = wx.getStorageSync('userInfo');
	if (!wxInfo) {
		return typeof cb == "function" && cb(userInfo);;
	}
	userInfo.wxInfo = wxInfo.userInfo
	// wx.setStorageSync('userInfo', userInfo);
	util.request({
		url: 'auth/session/userinfo',
		data: {
			signature: wxInfo.signature,
			rawData: wxInfo.rawData,
			iv: wxInfo.iv,
			encryptedData: wxInfo.encryptedData
		},
		method: 'POST',
		header: {
			'content-type': 'application/x-www-form-urlencoded'
		},
		cachetime: 0,
		success: function (res) {
			if (!res.data.errno) {
				userInfo.memberInfo = res.data.data;
				wx.setStorageSync('userInfo', userInfo);
			}
			typeof cb == "function" && cb(userInfo);
		}
	});
}
util.checkSession = function(option) {
	util.request({
		url: 'auth/session/check',
		method: 'POST',
		cachetime: 0,
		showLoading: false,
		success: function (res) {
			if (!res.data.errno) {
				typeof option.success == "function" && option.success();
			} else {
				typeof option.fail == "function" && option.fail();
			}
		},
		fail: function() {
			typeof option.fail == "function" && option.fail();
		}
	})
}
/*
* 
*/
util.getUserInfo = function (cb, wxInfo) {
	var login = function() {
		console.log('start login');
		var userInfo = {
			'sessionid': '',
			'wxInfo': '',
			'memberInfo': '',
		};
		wx.login({
			success: function (res) {
				util.getWe7User(function(userInfo) {
					if (wxInfo) {
						util.upadteUser(wxInfo, function(userInfo) {
							typeof cb == "function" && cb(userInfo);
						})
					} else {
						if (wx.canIUse('getUserInfo')) {
							// 
							wx.getUserInfo({
								withCredentials: true,
								success: function(wxInfo){
									util.upadteUser(wxInfo, function(userInfo) {
										typeof cb == "function" && cb(userInfo);
									})
								},
								fail: function() {
									typeof cb == "function" && cb(userInfo);
								}
							})
						} else {
							typeof cb == "function" && cb(userInfo);
						}
					}
				}, res.code)
			},
			fail: function () {
				wx.showModal({
					title: '',
					content: '',
					success: function (res) {
						if (res.confirm) {
							util.getUserInfo();
						}
					}
				})
			}
		});
	};

	var userInfo = wx.getStorageSync('userInfo') || {};
	if (userInfo.sessionid) {
		util.checkSession({
			success: function(){
				if (wxInfo) {
					util.upadteUser(wxInfo, function(userInfo) {
						typeof cb == "function" && cb(userInfo);
					})
				} else {
					typeof cb == "function" && cb(userInfo);
				}
			},
			fail: function(){
				userInfo.sessionid = '';
				console.log('relogin');
				wx.removeStorageSync('userInfo');
				login();
			}
		})
	} else {
		//
		login();
	}
}

util.navigateBack = function (obj) {
	let delta = obj.delta ? obj.delta : 1;
	if (obj.data) {
		let pages = getCurrentPages()
		let curPage = pages[pages.length - (delta + 1)];
		if (curPage.pageForResult) {
			curPage.pageForResult(obj.data);
		} else {
			curPage.setData(obj.data);
		}
	}
	wx.navigateBack({
		delta: delta, //  delta(1) 
		success: function (res) {
			// success
			typeof obj.success == "function" && obj.success(res);
		},
		fail: function (err) {
			// fail
			typeof obj.fail == "function" && obj.fail(err);
		},
		complete: function () {
			// complete
			typeof obj.complete == "function" && obj.complete();
		}
	})
};

util.footer = function ($this) {
	let app = getApp();
	let that = $this;
	let tabBar = app.tabBar;
	for (let i in tabBar['list']) {
		tabBar['list'][i]['pageUrl'] = tabBar['list'][i]['pagePath'].replace(/(\?|#)[^"]*/g, '')
	}
	that.setData({
		tabBar: tabBar,
		'tabBar.thisurl': that.__route__
	})
};
/*
 * 
 * type  success, error  success,  toast
 * redirect ,    
 * navigate:/we7/pages/detail/detail  navigateTo 
 * redirect:/we7/pages/detail/detail  redirectTo  redirect
*/
util.message = function(title, redirect, type) {
	if (!title) {
		return true;
	}
	if (typeof title == 'object') {
		redirect = title.redirect;
		type = title.type;
		title = title.title;
	}
	if (redirect) {
		var redirectType = redirect.substring(0, 9), url = '', redirectFunction = '';
		if (redirectType == 'navigate:') {
			redirectFunction = 'navigateTo';
			url = redirect.substring(9);
		} else if (redirectType == 'redirect:') {
			redirectFunction = 'redirectTo';
			url = redirect.substring(9);
		} else {
			url = redirect;
			redirectFunction = 'redirectTo';
		}
	}
	console.log(url)
	if (!type) {
		type = 'success';
	}

	if (type == 'success') {
		wx.showToast({
			title: title,
			icon: 'success',
			duration: 2000,
			mask : url ? true : false,
			complete : function() {
				if (url) {
					setTimeout(function(){
						wx[redirectFunction]({
							url: url,
						});
					}, 1800);
				}
				
			}
		});
	} else if (type == 'error') {
		wx.showModal({
			title: '',
			content : title,
			showCancel : false,
			complete : function() {
				if (url) {
					wx[redirectFunction]({
						url: url,
					});
				}
			}
		});
	}
}

