util.showLoading = function() {
	var isShowLoading = wx.getStorageSync('isShowLoading');
	if (isShowLoading) {
		wx.hideLoading();
		wx.setStorageSync('isShowLoading', false);
	}

	wx.showLoading({
		title : '',
		complete : function() {
			wx.setStorageSync('isShowLoading', true);
		},
		fail : function() {
			wx.setStorageSync('isShowLoading', false);
		}
	});
}

